h3,
h4 {
  padding: 0;
  margin: 0;
}

#site-wrapper {
  transition: 0.5s ease;
  position: relative;
  z-index: 10;
  background-color: #fff;
  overflow: clip;
  min-height: 100dvh;
}

#site-header {
  top: 0;
  z-index: 10;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}
@media only screen and (min-width: 768px) {
  .container {
    width: 750px;
  }
}
@media only screen and (min-width: 992px) {
  .container {
    width: 970px;
  }
}
@media only screen and (min-width: 1260px) {
  .container {
    width: 1200px;
  }
}

body.sidebar-active #site-wrapper {
  translate: -275px;
}
@media only screen and (min-width: 992px) {
  body.sidebar-active #site-wrapper {
    translate: -350px;
  }
}

#site-sidebar {
  display: grid;
  width: 275px;
  overflow-y: scroll;
  height: 100%;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 5;
}
@media only screen and (min-width: 992px) {
  #site-sidebar {
    width: 350px;
  }
}

body.sidebar-active #site-wrapper::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.4);
  z-index: 999;
  cursor: pointer;
}

#consent-btn-manage {
  position: fixed;
  bottom: 0;
  left: 0;
  background-color: #999AA2;
  color: #FFF;
  z-index: 50;
  font-size: 0.8rem;
  border: none;
  border-top-right-radius: 10px;
  cursor: pointer;
  padding: 0.3rem 0.4rem;
}

#site-consent {
  display: none;
}

#site-consent.active {
  display: grid;
  position: fixed;
  height: 100%;
  min-height: 100dvh;
  z-index: 100;
  background: rgba(0, 0, 0, 0.7);
  grid-template-columns: min(100%, 600px);
  justify-content: center;
  align-items: center;
}

#site-consent > div {
  margin: 2rem;
  display: grid;
  background-color: white;
  border-radius: 5px;
}

#site-consent > div > h2 {
  font-weight: 400;
  font-size: 1.6rem;
  padding: 1.5rem;
  color: #333645;
}

#site-consent > div > p {
  padding: 1.5rem;
  border: #ccc solid 1px;
  line-height: 1.7;
  margin: 0;
  color: #333645;
}
#site-consent > div > p a {
  text-decoration: none;
  line-height: 1.7;
  margin: 0;
  color: #333645;
}
#site-consent > div > p a:hover {
  text-decoration: underline;
}

.consent-buttons {
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
@media only screen and (min-width: 768px) {
  .consent-buttons {
    flex-direction: row;
  }
}

#consent-btn-settings,
#consent-btn-accept {
  display: block;
  color: #FFF;
  padding: 0.8rem;
  font-size: 1.125rem;
  text-align: center;
  border-radius: 3px;
  border: none;
  flex-grow: 1;
}
#consent-btn-settings:hover,
#consent-btn-accept:hover {
  cursor: pointer;
}

#consent-btn-settings {
  background-color: #999AA2;
}
#consent-btn-settings:hover {
  background-color: rgb(128.2846153846, 129.5269230769, 139.4653846154);
}

#consent-btn-accept {
  background-color: #926FB1;
}
#consent-btn-accept:hover {
  background-color: rgb(124.6054054054, 86.0108108108, 158.7891891892);
}

#site-header {
  position: relative;
  transition: 5s ease;
}

#site-header.sticky {
  position: sticky;
}

#header-top {
  padding: 20px 0;
  background-color: #FFF;
}

#header-top > div {
  display: grid;
  padding: 0 15px;
  grid-template-columns: 1fr repeat(2, max-content);
  grid-template-areas: "logo icon menu" "search search search";
  column-gap: 1rem;
  row-gap: 11px;
  align-items: center;
}

#header-logo {
  grid-area: logo;
  display: block;
  width: 100%;
  min-width: 100px;
  max-width: 402px;
  max-height: 70px;
}

#header-icon {
  grid-area: icon;
  font-size: 32px;
}

#header-support {
  background-color: #4183d7;
  border-color: #4183d7;
  color: #FFF;
  grid-area: support;
}
#header-support:hover {
  background-color: #2a6ec6;
  border-color: #2665b5;
}

#header-support:hover,
#header-contact:hover,
#header-form > button:hover {
  cursor: pointer;
}

#header-contact {
  background-color: #999aa2;
  border-color: #999aa2;
  color: #FFF;
  grid-area: contact;
}
#header-contact:hover {
  background-color: #84858f;
  border-color: #797a85;
}

#header-support,
#header-contact {
  display: none;
  text-transform: uppercase;
  height: 55px;
  border-radius: 3px;
  padding: 0 1rem;
  line-height: 46px;
  font-size: 18px;
  text-decoration: none;
}
#header-support p,
#header-contact p {
  display: inline-block;
  margin: 0;
}

#header-menu {
  grid-area: menu;
}

#header-form {
  width: 100%;
  grid-area: search;
  position: relative;
}
#header-form input {
  padding: 5px 15px;
  height: 47px;
  background-color: #e5e5e5;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  border: 2px solid rgba(0, 0, 0, 0.1);
  width: 100%;
  display: inline-block;
}
#header-form input:focus {
  box-shadow: 0 0 3px #fff;
  outline: 1px solid hsla(0, 0%, 100%, 0.2);
}
#header-form button {
  border: none;
  background-color: transparent;
  position: absolute;
  right: 5px;
  font-size: 20px;
  height: 100%;
}

@media only screen and (min-width: 768px) {
  #header-top > div {
    padding: 0 15px;
    margin: auto;
    grid-template-columns: 1fr repeat(2, max-content);
    grid-template-areas: "logo search menu";
  }
  #header-logo {
    width: 345px;
  }
  #header-icon {
    display: none;
  }
  #header-form input {
    height: 56px;
    width: 250px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    background: #FFF;
    border-radius: 0 3px 3px 0;
  }
  #header-form input:focus {
    border-color: #5bc0de;
    outline: 0;
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(91, 192, 222, 0.6);
  }
  #header-form button {
    right: 0px;
    padding: 7px 20px 7px;
    background-color: #999aa2;
    color: #FFF;
    font-size: 23px;
    border-radius: 0 3px 3px 0;
  }
  #header-form button:hover {
    background-color: #84858f;
  }
}
@media only screen and (min-width: 992px) {
  #header-top > div {
    grid-template-columns: 1fr repeat(4, max-content);
    grid-template-areas: "logo support contact search menu";
  }
  #header-logo {
    width: 100%;
  }
  #header-support,
  #header-contact {
    display: flex;
    align-items: center;
    column-gap: 5px;
  }
  #header-form input {
    display: none;
  }
  #header-form button {
    position: relative;
    height: 55px;
    padding: 0 15px;
    border-radius: 3px;
  }
}
@media only screen and (min-width: 1260px) {
  #header-form input {
    display: inline-block;
    height: 55px;
  }
  #header-form button {
    position: absolute;
    height: 100%;
    border-radius: 0 3px 3px 0;
  }
}
