#header-menu {
  grid-area: menu;
  background-color: #212330;
  display: flex;
  flex-direction: column;
  row-gap: 6px;
  justify-content: center;
  border-radius: 3px;
  padding: 15px;
  margin: 0;
  border: 0;
  height: 56px;
}
#header-menu:hover {
  cursor: pointer;
}

#menu-top,
#menu-middle,
#menu-bottom {
  content: "";
  background-color: #FFF;
  display: block;
  padding: 2px 17px;
  overflow: hidden;
  transition: 0.5s ease;
}

#header-menu.active #menu-middle {
  opacity: 0;
}

#header-menu.active #menu-top {
  transform: translate(0, 10px) rotate(45deg);
}

#header-menu.active #menu-bottom {
  transform: translate(0, -10px) rotate(-45deg);
}

